<template>
  <div class="throw-box">
    <throw-header :address="address"></throw-header>
    <div class="bottom-box">
      <div class="bottom-content">
        <span class="name">{{$t('nts.恭喜您获得')}}</span>
        <span class="num">{{buyer.includes((address + '').toLowerCase()) ? '20000' : '10000'}}</span>
        <span class="name">NTS!</span>
      </div>
      <div class="link-box item-box">
        <div class="title-box flex-middle">
          <img src="@/assets/images/new/link@2x.png" alt=""><span>{{$t('nts.您的推荐链接')}}</span>
        </div>
        <div class="link-content item-content">
          {{link}}<img class="copy clip-button" :data-clipboard-text="link" src="@/assets/images/new/copy@2x.png" />
        </div>
      </div>
      <div class="shequ-box item-box">
        <div class="title-box flex-middle">
          <img src="@/assets/images/new/shequ@2x.png" alt=""><span>{{$t('nts.您的社区')}}</span>
        </div>
        <div class="shequ-content item-content">
          <div class="shequ-item flex-middle border-1px-b" v-for="(item, index) in children" :key="index">
            <span class="num">{{index+1}}</span><span class="text-ellipsis name flex-item">{{item}}</span>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
import ThrowHeader from './ThrowHeader'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import buyer from "@/utils/buyer";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import Clipboard from 'clipboard';
import { Toast } from 'vant';
// import BigNumber from 'bignumber.js';
export default {
  components: {
    ThrowHeader
  },
  setup() {
    var clipboard = new Clipboard('.copy')
    clipboard.on('success', function (e) {
      Toast(t('share.复制成功'))
      e.clearSelection()
    })
    clipboard.on('error', function () {
      Toast(t('share.复制失败，请手动长按复制'))
    }) 
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,   
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      investContract: null,
      usdtContract: null,
      children: [],
      buyer: buyer,
      link: '',
      initContract: () => {
        data.link = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.initData()
      },
      initData: () => {
        data.getChildren()
      },
      getChildren: async () => {
        let children = await data.investContract.methods.getChilren(data.address).call()
        data.children = children
      },
      timeEnd: ()=>{}
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
  .bottom-box {
    text-align: center;
    margin: 0 15px;
    overflow: hidden;
    padding-bottom: 30px;
    .bottom-content {
      width: 100%;
      line-height: 64px;
      margin-top: 24px;
      border: 1px dashed #9752C7;
      font-size: 20px;
      font-weight: bold;
      background: #fff;
      border-radius: 32px;
      .num {
        color: #9752C7;
      }
      .name {
        color: #151515;
      }
    }
    .title-box {
      img {
        display: block;
        width: 17px;
        height: 17px;
        margin-right: 8px;
      }
      span {
        color: #151515;
        font-size: 15px;
        font-weight: bold;
      }
    }
    .item-box {
      margin-top: 25px;
      .item-content {
        background: #fff;
        border-radius: 5px;
        text-align: left;
        width: 100%;
        box-sizing: border-box;
        margin-top: 15px;
        padding: 0 15px;
        font-size: 14px;
        line-height: 2;
        word-break: break-all;
      }
    }
    .link-box {
      .link-content {
        padding: 20px 15px;
        color: #666;
        img {
          display: inline-block;
          width: 16px;
          height: 16px;
          margin-left: 8px;
        }
      }
    }
    .shequ-box {
      .shequ-content {
        .shequ-item {
          line-height: 45px;
          .num {
            display: inline-block;
            color: #666666;
            width: 30px;
            text-align: center;
          }
          .name {
            color: #151515;
          }
        }
      }
    }
  }
</style>